@import url(./vendor/swiper.min.css);
:root {
  --color-black: #000;
  --color-white: #fff;
  --color-dark: #151515;
  --quill-gray: #d9d9d7;
}

@font-face {
  font-family: "Jost";
  src: url("../fonts/Jost-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Jost";
  src: url("../fonts/Jost-Medium.woff2") format("woff2");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Jost";
  src: url("../fonts/Jost-MediumItalic.woff2") format("woff2");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}

