html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "Jost", sans-serif;
  font-weight: 500;
  color: var(--color-dark);
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.site-container {
  overflow: hidden;
}

.stop-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.is-hidden {
  display: none !important;
}

.for-mobile {
  display: none;
}

@media (max-width: 1024px) {
  .for-mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 40px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.header {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 100;
  width: 100%;
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 36px;
  padding-bottom: 36px;
}

.header__shop-nav {
  margin-left: 250px;
}

.logo {
  display: inline-block;
  width: 117px;
  height: 22px;
}

.nav__list {
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 45px;
}

.nav__link {
  position: relative;
  font-size: 15px;
}

.nav__link::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: -5px;
  width: 0;
  height: 2px;
  background-color: var(--color-dark);
  transition: width 0.15s;
}

.nav__link:hover::before {
  width: 100%;
  transition: width 0.15s;
}

.nav__link--current::before {
  width: 100%;
  transition: width 0.15s;
}

.shop-nav {
  display: flex;
  align-items: center;
}

.shop-nav__item:not(:last-child) {
  margin-right: 25px;
}

.shop-nav__link {
  display: inline-block;
  width: 16px;
  height: 16px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.shop-nav__link--user {
  background-image: url("../img/user.png");
}

.cart {
  width: auto;
  height: auto;
}

.cart__icon {
  display: inline-block;
  width: 16px;
  height: 16px;
  background-image: url("../img/cart.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.cart__quantity {
  display: inline-flex;
  align-items: center;
  border-radius: 25px;
  padding: 0 5px;
  height: 16px;
  font-weight: 700;
  font-size: 10px;
  color: var(--color-white);
  background-color: var(--color-dark);
}

.search-form__btn {
  display: inline-block;
  width: 16px;
  height: 16px;
  background-image: url("../img/search.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.hero {
  position: relative;
  height: 100vh;
  min-height: 700px;
  background-color: var(--quill-gray);
}

.hero::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: rgba(var(--color-black), 0.05);
}

.hero__container {
  position: relative;
  height: 100%;
}

.hero__title {
  margin: 0;
  margin-bottom: 40px;
  max-width: 560px;
  font-weight: 700;
  font-size: 72px;
}

.hero__descr {
  margin: 0;
  font-size: 18px;
}

.hero__image {
  position: absolute;
  right: 273px;
  bottom: 0;
  max-height: 90%;
}

.hero__content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  padding: 0 260px;
  height: 100%;
}

.banner-slider {
  overflow: hidden;
  width: 100%;
  height: 100%;
}

.banner-slider .swiper-slide {
  height: 100%;
}

.banner-pag {
  left: 50%;
  bottom: 46px;
  transform: translateX(-50%);
}

.banner-pag .swiper-pagination-bullet {
  margin: 0 12px;
  width: 6px;
  height: 6px;
  background-color: var(--color-white);
  opacity: 0.5;
}

.banner-pag .swiper-pagination-bullet-active {
  position: relative;
  background-color: var(--color-dark);
  opacity: 1;
}

.banner-pag .swiper-pagination-bullet-active::before {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  z-index: -1;
  border-radius: 100%;
  width: 20px;
  height: 20px;
  background-color: var(--color-white);
  transform: translate(-50%, -50%);
}

.marketing {
  position: absolute;
  left: 50px;
  bottom: 50px;
  z-index: 10;
  display: flex;
  padding: 10px;
  width: 280px;
  box-shadow: 0 5px 30px rgba(0, 0, 0, 0.1);
  background-color: var(--color-white);
  transform: translateX(-150vw);
  transition: transform 0.3s;
}

.marketing--visible {
  transform: translateX(0);
  transition: transform 0.3s;
}

.marketing__text {
  margin-left: 10px;
}

.marketing__image {
  flex-shrink: 0;
}

.marketing__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.marketing__descr {
  display: block;
  margin-bottom: 5px;
  font-size: 11px;
  color: var(--color-gray);
}

.marketing__title {
  margin: 0;
  margin-bottom: 7px;
  padding-right: 20px;
  min-height: 34px;
  font-size: 15px;
}

.marketing__when-from {
  display: block;
  padding-right: 40px;
  font-size: 11px;
  color: var(--color-gray);
}

.marketing__close {
  position: absolute;
  right: 10px;
  top: 10px;
  padding: 0;
  width: 10px;
  height: 10px;
  background-image: url("../img/close.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
